
body {
    font-family: 'Great Vibes', cursive;
    text-align: center;
    margin: 0;
    background-color: #f9f4f5; /* Soft pastel color for the background */
    background-image: url("pngtree-small-fresh-and-beautiful-romantic-dating-bnner-background-image_223606.jpg");
    background-repeat: no-repeat-y;
    background-size: cover;
    height: 100%;
    width: 100%;
}

.container {
    padding: 30px;
}

h1 {
    font-size: 36px;
    margin-bottom: 30px;
    color: #e91e63; /* Romantic color for the heading */
}

.buttons {
    margin: 20px;
}

button {
    font-size: 20px;
    padding: 12px 24px;
    margin: 10px;
    border: none;
    border-radius: 20px;
    background-color: #ff4081; /* Romantic color for the buttons */
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #ff5c8d; /* Lighter shade on hover for a soft effect */
}

.popup {
    background-color: #fff;
    border: 1px solid #e91e63; /* Romantic color for the border */
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    padding: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
    font-family: 'Dancing Script', cursive;
    font-size: 24px;
    color: #e91e63; /* Romantic color for the popup text */
}

/* Add a smooth fade-in animation for the popup */
.popup.fade-in {
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

